import React from 'react';
import { Typography, Paper, Card, CardContent, CardMedia, Grid, Chip, Button, Box } from '@mui/material';
import GitHubIcon from '@mui/icons-material/GitHub';
import LinkIcon from '@mui/icons-material/Link';

interface Project {
    name: string;
    description: string;
    image: string;
    technologies: string[];
    githubLink?: string;
    liveLink?: string;
}

const Projects: React.FC = () => {
    const projects: Project[] = [
        {
            name: 'Project 1',
            description: 'プロジェクト1の詳細な説明。このプロジェクトでは〇〇を実現しました。',
            image: 'https://via.placeholder.com/300x200',
            technologies: ['React', 'TypeScript', 'Material-UI'],
            githubLink: 'https://github.com/yourusername/project1',
            // liveLink: 'https://project1.example.com'
        },
        {
            name: 'Project 2',
            description: 'プロジェクト2の詳細な説明。このアプリケーションは〇〇の問題を解決します。',
            image: 'https://via.placeholder.com/300x200',
            technologies: ['Node.js', 'Express', 'MongoDB'],
            githubLink: 'https://github.com/yourusername/project2'
        },
        // 他のプロジェクトを追加...
    ];

    return (
        <Paper elevation={3} sx={{ p: 3, my: 3 }}>
            <Typography variant="h4" component="h2" gutterBottom>
                My Projects
            </Typography>
            <Grid container spacing={3}>
                {projects.map((project, index) => (
                    <Grid item xs={12} sm={6} md={4} key={index}>
                        <Card sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
                            <CardMedia
                                component="img"
                                height="140"
                                image={project.image}
                                alt={project.name}
                            />
                            <CardContent sx={{ flexGrow: 1 }}>
                                <Typography variant="h6" component="h3" gutterBottom>
                                    {project.name}
                                </Typography>
                                <Typography variant="body2" paragraph>
                                    {project.description}
                                </Typography>
                                <Box sx={{ mb: 1 }}>
                                    {project.technologies.map((tech, techIndex) => (
                                        <Chip
                                            key={techIndex}
                                            label={tech}
                                            size="small"
                                            sx={{ mr: 0.5, mb: 0.5 }}
                                        />
                                    ))}
                                </Box>
                            </CardContent>
                            <Box sx={{ p: 2, pt: 0 }}>
                                {project.githubLink && (
                                    <Button
                                        startIcon={<GitHubIcon />}
                                        href={project.githubLink}
                                        target="_blank"
                                        rel="noopener noreferrer"
                                        sx={{ mr: 1 }}
                                    >
                                        GitHub
                                    </Button>
                                )}
                                {project.liveLink && (
                                    <Button
                                        startIcon={<LinkIcon />}
                                        href={project.liveLink}
                                        target="_blank"
                                        rel="noopener noreferrer"
                                    >
                                        Live Demo
                                    </Button>
                                )}
                            </Box>
                        </Card>
                    </Grid>
                ))}
            </Grid>
        </Paper>
    );
};

export default Projects;