import React, { useState } from 'react';
import { Typography, Paper, TextField, Button, Box, Alert } from '@mui/material';
import emailjs from 'emailjs-com';

const Contact: React.FC = () => {
    const [formState, setFormState] = useState({
        name: '',
        email: '',
        message: ''
    });

    const [emailSent, setEmailSent] = useState(false);
    const [error, setError] = useState(false);

    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = event.target;
        setFormState({ ...formState, [name]: value });
    };

    const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        emailjs.send(
            'service_ww8l48e', // サービスID
            'template_l3yl53m', // テンプレートID
            formState,
            '-wyaD8g5KrR9pkF7w' // 公開鍵
        ).then(() => {
            setEmailSent(true);
            setError(false);
            setFormState({ name: '', email: '', message: '' });
        }).catch((error) => {
            console.error('Email send error:', error);
            setEmailSent(false);
            setError(true);
        });
    };

    return (
        <Paper elevation={3} sx={{ p: 3, my: 3 }}>
            <Typography variant="h4" component="h2" gutterBottom>
                Contact Me
            </Typography>
            {emailSent && <Alert severity="success">Email sent successfully!</Alert>}
            {error && <Alert severity="error">Failed to send email. Please try again later.</Alert>}
            <Box component="form" onSubmit={handleSubmit} noValidate autoComplete="off" sx={{ mt: 2 }}>
                <TextField
                    fullWidth
                    label="Name"
                    name="name"
                    variant="outlined"
                    margin="normal"
                    required
                    value={formState.name}
                    onChange={handleChange}
                />
                <TextField
                    fullWidth
                    label="Email"
                    name="email"
                    variant="outlined"
                    margin="normal"
                    required
                    type="email"
                    value={formState.email}
                    onChange={handleChange}
                />
                <TextField
                    fullWidth
                    label="Message"
                    name="message"
                    variant="outlined"
                    margin="normal"
                    required
                    multiline
                    rows={4}
                    value={formState.message}
                    onChange={handleChange}
                />
                <Button type="submit" variant="contained" color="primary" sx={{ mt: 2 }}>
                    Send
                </Button>
            </Box>
        </Paper>
    );
};

export default Contact;
