import React from 'react';
import { Typography, Paper, Chip, Box, Grid, LinearProgress } from '@mui/material';

interface Skill {
    name: string;
    level: number; // 0 to 100
    category: 'Frontend' | 'Backend' | 'Language' | 'Other';
}

const Skills: React.FC = () => {
    const skills: Skill[] = [
        { name: 'React', level: 60, category: 'Frontend' },
        { name: 'TypeScript', level: 65, category: 'Language' },
        { name: 'JavaScript', level: 65, category: 'Language' },
        { name: 'Python', level: 80, category: 'Language' },
        { name: 'Java', level: 25, category: 'Language' },
        { name: 'C言語', level: 70, category: 'Language' },
        { name: 'Node.js', level: 45, category: 'Backend' },
        { name: 'HTML/CSS', level: 85, category: 'Frontend' },
        { name: 'Git', level: 50, category: 'Other' },
        { name: 'SQL', level: 60, category: 'Backend' },
    ];

    const categories = ['Frontend', 'Backend', 'Language', 'Other'];

    const getColor = (level: number) => {
        if (level < 50) return 'error';
        if (level < 70) return 'warning';
        if (level < 90) return 'info';
        return 'success';
    };

    return (
        <Paper elevation={3} sx={{ p: 3, my: 3 }}>
            <Typography variant="h4" component="h2" gutterBottom>
                My Skills
            </Typography>
            <Grid container spacing={3}>
                {categories.map((category) => (
                    <Grid item xs={12} md={6} key={category}>
                        <Typography variant="h6" gutterBottom>
                            {category}
                        </Typography>
                        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
                            {skills
                                .filter((skill) => skill.category === category)
                                .map((skill, index) => (
                                    <Box key={index}>
                                        <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                                            <Typography variant="body1">{skill.name}</Typography>
                                            <Typography variant="body2">{skill.level}%</Typography>
                                        </Box>
                                        <LinearProgress
                                            variant="determinate"
                                            value={skill.level}
                                            color={getColor(skill.level)}
                                        />
                                    </Box>
                                ))}
                        </Box>
                    </Grid>
                ))}
            </Grid>
        </Paper>
    );
};

export default Skills;