import React from 'react';
import { Typography, Paper, Box, Avatar, Grid, Chip } from '@mui/material';
import { Timeline, TimelineItem, TimelineSeparator, TimelineConnector, TimelineContent, TimelineDot } from '@mui/lab';
import SchoolIcon from '@mui/icons-material/School';
import WorkIcon from '@mui/icons-material/Work';

import imge from '../assets/images/imge.jpg'

const About: React.FC = () => {
    const interests = ['Web開発', '機械学習', 'UI/UXデザイン', 'オープンソース'];

    return (
        <Paper elevation={3} sx={{ p: 3, my: 3 }}>
            <Grid container spacing={3}>
                <Grid item xs={12} md={4}>
                    <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                        <Avatar
                            alt="玉城洵弥"
                            src={imge}
                            sx={{ width: 200, height: 200, mb: 2 }}
                        />
                        <Typography variant="h4" component="h2" gutterBottom>
                            玉城洵弥
                        </Typography>
                        <Typography variant="subtitle1" gutterBottom>
                            ソフトウェアエンジニア
                        </Typography>
                    </Box>
                </Grid>
                <Grid item xs={12} md={8}>
                    <Typography variant="h5" gutterBottom>
                        About Me
                    </Typography>
                    <Typography variant="body1" paragraph>
                        私は、最新のWeb技術に情熱を持つソフトウェアエンジニアです。ユーザーエクスペリエンスを向上させる革新的なソリューションの開発に取り組んでいます。チームワークを重視し、常に新しい技術を学び続けることを心がけています。
                    </Typography>
                    <Typography variant="h6" gutterBottom>
                        興味・関心
                    </Typography>
                    <Box sx={{ mb: 2 }}>
                        {interests.map((interest, index) => (
                            <Chip key={index} label={interest} sx={{ mr: 1, mb: 1 }} />
                        ))}
                    </Box>
                    <Typography variant="h6" gutterBottom>
                        経歴
                    </Typography>
                    <Timeline>
                        <TimelineItem>
                            <TimelineSeparator>
                                <TimelineDot color="primary">
                                    <SchoolIcon />
                                </TimelineDot>
                                <TimelineConnector />
                            </TimelineSeparator>
                            <TimelineContent>
                                <Typography variant="h6" component="span">
                                    沖縄工業高等専門学校
                                </Typography>
                                <Typography>2023年3月</Typography>
                            </TimelineContent>
                        </TimelineItem>
                        <TimelineItem>
                            <TimelineSeparator>
                                <TimelineDot color="primary">
                                    <SchoolIcon />
                                </TimelineDot>
                            </TimelineSeparator>
                            <TimelineContent>
                                <Typography variant="h6" component="span">
                                    岐阜大学 在学中
                                </Typography>
                                <Typography>2024年4月 - 現在</Typography>
                            </TimelineContent>
                        </TimelineItem>
                    </Timeline>
                </Grid>
            </Grid>
        </Paper>
    );
};

export default About;